body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App h1 {
  font-size: 1.5em;
}

.App > div {
  display: inline-block;
  margin: 1em
}

.outer {
    display: -webkit-inline-flex;
    display: inline-flex;
    overflow-x: scroll;
    width: calc(21em + 6px);
    -webkit-scroll-snap-type: x mandatory;
        -ms-scroll-snap-type: x mandatory;
            scroll-snap-type: x mandatory;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
}

.wrapper {
    border: solid black 1px;
    width: 6em;
    height: 7em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    scroll-snap-align: center;
    text-align: center;
    padding: 0 0.5em;
}

.center {
    -webkit-align-self: center;
            align-self: center;
    font-size: 3em;
    width: 33%;
}

.top {
    -webkit-order: -1;
            order: -1;
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
}

.bottom {
    -webkit-order: 1;
            order: 1;
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
}

.left {
    text-align: left;
    /* padding-left: 0.5px; */
}

.right {
    text-align: right;
    /* padding-right: 2px; */
}

.DiceNumberSelector-wrapper {
    width: calc(4em + 8px + 0.8em);
    display: inline-block;
    height: calc(12em + 18px + 2em);
}
.DiceNumberSelector-container {
    width: calc(4em + 8px + 0.8em);
    display: -webkit-flex;
    display: flex;
    height: calc(8em + 16px + 1.6em);
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0;
    margin: 0;
}

.DiceNumberSelector-element.d0 {
    width: 4em;
    height: 4em;
    margin: auto;
}

.DiceNumberSelector-element {
    width: 2em;
    height: 2em;
    text-align: center;
    display: table;
    border: 2px gray solid;
    border-radius: 50%;
    margin: 0.2em;
    padding: 0;
}
.DiceNumberSelector-element div {
    display: table-cell;
    vertical-align: middle;
}

.DiceNumberSelector-element.selected {
    border: 2px red solid;

}
